);\n  $group: null;\n  @if ($slash) {\n    $group: str-slice($name, 0, $slash - 1);\n    $name: str-slice($name, $slash + 1);\n  } @else {\n    $group: nth(map-keys($__iconfont__data), 1);\n  }\n  $group: iconfont-group($group);\n  @if (false == map-has-key($group, $name)) {\n    @warn 'Undefined Iconfont Glyph!';\n    @return '';\n  }\n  @return map-get($group, $name);\n}\n\n// complete mixing to include the icon\n// usage:\n// .my_icon{ @include iconfont('star') }\n@mixin iconfont($icon) {\n  $slash: str-index($icon, '/');\n  $group: null;\n  @if ($slash) {\n    $group: str-slice($icon, 0, $slash - 1);\n  } @else {\n    $group: nth(map-keys($__iconfont__data), 1);\n  }\n  &:before {\n    font-family: $group;\n    font-style: normal;\n    font-weight: 400;\n    content: iconfont-item($icon);\n  }\n}\n\n// creates the font face tag if the variable is set to true (default)\n@if $create-font-face == true {\n  @font-face {\n   font-family: \"iconfont-vue-e08a231\";\n   src: url('../iconfont-vue-e08a231.eot'); /* IE9 Compat Modes */\n   src: url('../iconfont-vue-e08a231.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */\n      url('../iconfont-vue-e08a231.woff') format('woff'), /* Pretty Modern Browsers */\n      url('../iconfont-vue-e08a231.ttf')  format('truetype'), /* Safari, Android, iOS */\n      url('../iconfont-vue-e08a231.svg') format('svg'); /* Legacy iOS */\n  }\n}\n\n// creates icon classes for each individual loaded svg (default)\n@if $create-icon-classes == true {\n  .#{$icon-common-class} {\n    font-style: normal;\n    font-weight: 400;\n\n    @each $icon, $content in map-get($__iconfont__data, \"iconfont-vue-e08a231\") {\n      &.#{$icon-prefix}#{$icon}:before {\n        font-family: \"iconfont-vue-e08a231\";\n        content: iconfont-item(\"iconfont-vue-e08a231/#{$icon}\");\n      }\n    }\n  }\n}\n","$scope_version:\"e08a231\"; @import 'variables';\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n